                type: 'warn',
                title: 'Произошла ошибка',
                text: 'Активируйте аккаунт для выполнения данного действия!',
                speed: 500,
                duration: 6000
            })
        });

        $('.krep1').click(function(){
            $('.inp_file').click();

        });

        $('.krep2').click(function(){
            $('.inp_file2').click();
        });
    }
}
</script>

